import * as THREE from "three";
import React, { useRef, useState, useMemo, useEffect } from "react";
import { extend, useFrame, useLoader } from "@react-three/fiber";
import { PlaneBufferGeometry } from "three";

const uniforms = {
    uTime: { value: 0 },
    bigElevation: { value: 0.4 },
    bigFrequency: { value: new THREE.Vector2(0.7, 0.9) },
    bigSpeed: { value: 0.5 },
}

const shaderModifier = (shader) => {
    shader.uniforms.uTime = uniforms.uTime;
    shader.uniforms.bigElevation = uniforms.bigElevation;
    shader.uniforms.bigFrequency = uniforms.bigFrequency;
    shader.uniforms.bigSpeed = uniforms.bigSpeed;
    // add functions and uniforms
    shader.vertexShader = shader.vertexShader.replace(
        '#include <common>',
        `
            #include <common>
            uniform float uTime;
            uniform float bigElevation;
            uniform vec2 bigFrequency;
            uniform float bigSpeed;

            float calcElevation(vec3 position)
            {
                return sin(position.x * bigFrequency.x + uTime * bigSpeed) * sin(position.y * bigFrequency.y + uTime * bigSpeed) * bigElevation;
            }

            vec4 permute(vec4 x)
            {
                return mod(((x*34.0)+1.0)*x, 289.0);
            }
            vec4 taylorInvSqrt(vec4 r)
            {
                return 1.79284291400159 - 0.85373472095314 * r;
            }
            vec3 fade(vec3 t)
            {
                return t*t*t*(t*(t*6.0-15.0)+10.0);
            }

            float cnoise(vec3 P)
            {
                vec3 Pi0 = floor(P); // Integer part for indexing
                vec3 Pi1 = Pi0 + vec3(1.0); // Integer part + 1
                Pi0 = mod(Pi0, 289.0);
                Pi1 = mod(Pi1, 289.0);
                vec3 Pf0 = fract(P); // Fractional part for interpolation
                vec3 Pf1 = Pf0 - vec3(1.0); // Fractional part - 1.0
                vec4 ix = vec4(Pi0.x, Pi1.x, Pi0.x, Pi1.x);
                vec4 iy = vec4(Pi0.yy, Pi1.yy);
                vec4 iz0 = Pi0.zzzz;
                vec4 iz1 = Pi1.zzzz;

                vec4 ixy = permute(permute(ix) + iy);
                vec4 ixy0 = permute(ixy + iz0);
                vec4 ixy1 = permute(ixy + iz1);

                vec4 gx0 = ixy0 / 7.0;
                vec4 gy0 = fract(floor(gx0) / 7.0) - 0.5;
                gx0 = fract(gx0);
                vec4 gz0 = vec4(0.5) - abs(gx0) - abs(gy0);
                vec4 sz0 = step(gz0, vec4(0.0));
                gx0 -= sz0 * (step(0.0, gx0) - 0.5);
                gy0 -= sz0 * (step(0.0, gy0) - 0.5);

                vec4 gx1 = ixy1 / 7.0;
                vec4 gy1 = fract(floor(gx1) / 7.0) - 0.5;
                gx1 = fract(gx1);
                vec4 gz1 = vec4(0.5) - abs(gx1) - abs(gy1);
                vec4 sz1 = step(gz1, vec4(0.0));
                gx1 -= sz1 * (step(0.0, gx1) - 0.5);
                gy1 -= sz1 * (step(0.0, gy1) - 0.5);

                vec3 g000 = vec3(gx0.x,gy0.x,gz0.x);
                vec3 g100 = vec3(gx0.y,gy0.y,gz0.y);
                vec3 g010 = vec3(gx0.z,gy0.z,gz0.z);
                vec3 g110 = vec3(gx0.w,gy0.w,gz0.w);
                vec3 g001 = vec3(gx1.x,gy1.x,gz1.x);
                vec3 g101 = vec3(gx1.y,gy1.y,gz1.y);
                vec3 g011 = vec3(gx1.z,gy1.z,gz1.z);
                vec3 g111 = vec3(gx1.w,gy1.w,gz1.w);

                vec4 norm0 = taylorInvSqrt(vec4(dot(g000, g000), dot(g010, g010), dot(g100, g100), dot(g110, g110)));
                g000 *= norm0.x;
                g010 *= norm0.y;
                g100 *= norm0.z;
                g110 *= norm0.w;
                vec4 norm1 = taylorInvSqrt(vec4(dot(g001, g001), dot(g011, g011), dot(g101, g101), dot(g111, g111)));
                g001 *= norm1.x;
                g011 *= norm1.y;
                g101 *= norm1.z;
                g111 *= norm1.w;

                float n000 = dot(g000, Pf0);
                float n100 = dot(g100, vec3(Pf1.x, Pf0.yz));
                float n010 = dot(g010, vec3(Pf0.x, Pf1.y, Pf0.z));
                float n110 = dot(g110, vec3(Pf1.xy, Pf0.z));
                float n001 = dot(g001, vec3(Pf0.xy, Pf1.z));
                float n101 = dot(g101, vec3(Pf1.x, Pf0.y, Pf1.z));
                float n011 = dot(g011, vec3(Pf0.x, Pf1.yz));
                float n111 = dot(g111, Pf1);

                vec3 fade_xyz = fade(Pf0);
                vec4 n_z = mix(vec4(n000, n100, n010, n110), vec4(n001, n101, n011, n111), fade_xyz.z);
                vec2 n_yz = mix(n_z.xy, n_z.zw, fade_xyz.y);
                float n_xyz = mix(n_yz.x, n_yz.y, fade_xyz.x); 
                return 2.2 * n_xyz;
            }
        `
    )
    // actual calculation
    shader.vertexShader = shader.vertexShader.replace(
        "#include <begin_vertex>",
        `
            #include <begin_vertex>
            
            for(float i = 1.0; i <= 3.0; i++)
            {
                transformed.z = calcElevation(position) - abs(cnoise(vec3(position.xy * 3.0 * i, uTime * 0.15)) * 1.1 / i);
            }
        `,
    );
    // shader.vertexShader = shader.vertexShader.replace(
    //     '#include <beginnormal_vertex>',
    //     `
    //         #include <beginnormal_vertex>

    //         vec3 currPos = vec3(position.x, position.y, calcElevation(position));
    //         float increment = 5.0/16.0;

    //         vec3 rightPos = vec3(position.x + increment, position.y, position.z);
    //         vec3 bottomPos = vec3(position.x, position.y - increment, position.z);
    //         vec3 elevatedRight = vec3(position.x + increment, position.y, calcElevation(rightPos));
    //         vec3 elevatedBottom = vec3(position.x, position.y - increment, calcElevation(bottomPos));
    //         vec3 edgeRight = elevatedRight - currPos;
    //         vec3 edgeBottom = elevatedBottom - currPos;

    //         vec3 crossProduct = cross(edgeBottom, edgeRight);
    //         objectNormal = crossProduct;
    //     `
    // )
}


export function Ocean(props) {
    const oceanRef = useRef();
    // Subscribe this component to the render-loop, rotate the mesh every frame
    useFrame(({ clock }) => {
        uniforms.uTime.value = clock.getElapsedTime();
    });
    // Return the view, these are regular Threejs elements expressed in JSX
    return (
        <mesh
            {...props}
            ref={oceanRef}
            rotation-x={-Math.PI / 2}
            receiveShadow={true}
        >

            <planeBufferGeometry
                args={[256, 128, 144, 108]}
            />
            <meshPhongMaterial
                color={new THREE.Color(0x00081b)}
                onBeforeCompile={shaderModifier}
                transparent={true}
                opacity={0.8}
                flatShading={true}
                shininess={80}
                
            />
        </mesh>
    )
}

export function OceanBed() {
    return (
        <mesh
            // rotation-x={-Math.PI / 2}
            position={[0, 3, -5]}
        >
            <planeBufferGeometry
                args={[100, 40]}
            />
            <meshPhongMaterial
                color={new THREE.Color(0x000000)}
                flatShading={true}
                shininess={0}
                emmisive={0x7799aa}
            />
        </mesh>
    )
}

export function Box(props) {
    // This reference gives us direct access to the THREE.Mesh object
    const ref = useRef()
    // Hold state for hovered and clicked events
    const [hovered, hover] = useState(false)
    const [clicked, click] = useState(false)
    // Subscribe this component to the render-loop, rotate the mesh every frame
    useFrame((state, delta) => (ref.current.rotation.x += 0.01))
    // Return the view, these are regular Threejs elements expressed in JSX
    return (
        <mesh
            {...props}
            ref={ref}
            scale={clicked ? 1.5 : 1}
            onClick={(event) => click(!clicked)}
            onPointerOver={(event) => hover(true)}
            onPointerOut={(event) => hover(false)}>
            <boxGeometry args={[1, 1, 1]} />
            <meshStandardMaterial color={hovered ? 'hotpink' : 'orange'} />
        </mesh>
    )
}

